import { ValidationError } from './../../validation';
import type { AxiosResponse } from 'axios';
import type { CreateAxiosOptions, RequestOptions, Result } from './types';
import { VAxios } from './Axios';
// import { getToken } from '@/utils/auth';
import { accountStore } from '@/store/modules/account';
import { AxiosTransform } from './axiosTransform';

import { checkStatus } from './checkStatus';

import { RequestEnum, ResultEnum, ContentTypeEnum } from './types';

import { isString } from '@/utils/is';
import { setObjToUrlParams, deepMerge } from '@/utils';
import { errorResult } from './const';
import { useI18n } from '@/hooks/useI18n';
import { createNow, formatRequestDate } from './helper';

const apiUrl = process.env.apiUrl;
const prefix = 'v1';

/**
 * @description: 
 */
const transform: AxiosTransform = {
  /**
   * @description: 
   */
  transformRequestData: (res: AxiosResponse<Result>, options: RequestOptions) => {
    const { t } = useI18n();
    const { isTransformRequestResult } = options;
    // 
    // codedata
    if (!isTransformRequestResult) {
      return res.data;
    }
    // 

    if (!res.data) {
      // data;
      return errorResult;
    }
    //   codedata  types.ts
    const { code, data } = res.data;

    // 
    if (code === ResultEnum.SUCCESS) {
      return data;
    }
    // 
    if (code === ResultEnum.ERROR) {
      // @todo 
    }
    return errorResult;
  },

  // config
  beforeRequestHook: (config, options) => {
    const { apiUrl, joinPrefix, joinParamsToUrl, formatDate, joinTime = true } = options;

    if (joinPrefix) {
      config.url = `${prefix}${config.url}`;
    }

    if (apiUrl && isString(apiUrl)) {
      config.url = `${apiUrl}${config.url}`;
    }
    const params = config.params || {};
    if (config.method?.toUpperCase() === RequestEnum.GET) {
      if (!isString(params)) {
        //  get 
        config.params = Object.assign(params || {}, createNow(joinTime, false));
      } else {
        // restful
        config.url = config.url + params + `${createNow(joinTime, true)}`;
        config.params = undefined;
      }
    } else {
      if (!isString(params)) {
        formatDate && formatRequestDate(params);
        config.data = params;
        config.params = undefined;
        if (joinParamsToUrl) {
          config.url = setObjToUrlParams(config.url as string, config.data);
        }
      } else {
        // restful
        config.url = config.url + params;
        config.params = undefined;
      }
    }
    return config;
  },

  /**
   * @description: 
   */
  requestInterceptors: (config) => {
    // config
    // const token = getToken();
    const address = accountStore.getToken.toLocaleLowerCase();
    // console.log(address, ':addressxxx');

    if (address) {
      config.headers.Authorization = address;
    }
    // if (token) {
    // jwt token
    // config.headers.Authorization = token;
    // }
    return config;
  },

  /**
   * @description: 
   */
  responseInterceptorsCatch: (error: any) => {
    const { t } = useI18n();
    // errorStore.setupErrorHandle(error);
    const { response, code, message } = error || {};
    const msg: string = response?.data?.error?.message ?? '';
    const err: string = error?.toString?.() ?? '';
    try {
      if (code === 'ECONNABORTED' && message.indexOf('timeout') !== -1) {
        alert(t('sys.api.apiTimeoutMessage'));
      }
      if (err?.includes('Network Error')) {
        alert(t('sys.api.networkException'));
      }
    } catch (error) {
      throw new Error(error);
    }
    checkStatus(error?.response?.status, msg);
    return Promise.reject(error);
  },
};

function createAxios(opt?: Partial<CreateAxiosOptions>) {
  return new VAxios(
    deepMerge(
      {
        timeout: 10 * 1000,
        // 
        // baseURL: globSetting.apiUrl,
        // 
        // prefixUrl: prefix,
        headers: { 'Content-Type': ContentTypeEnum.JSON },
        // form-data
        // headers: { 'Content-Type': ContentTypeEnum.FORM_URLENCODED },
        // 
        transform,
        // 
        requestOptions: {
          // buprefix url
          joinPrefix: true,
          // 
          isTransformRequestResult: true,
          // posturl
          joinParamsToUrl: false,
          // 
          formatDate: true,
          // 
          errorMessageMode: 'message',
          // 
          apiUrl: apiUrl,
          //  
          joinTime: true,
        },
      },
      opt || {}
    )
  );
}
export const defHttp = createAxios();

